import { Directive, ElementRef, EventEmitter, HostListener, Output } from '@angular/core';
import { environment } from 'src/environments/environment';

@Directive({
  selector: '[duelitPaypal]'
})
export class PaypalDirective {
  @Output()
  readonly opened = new EventEmitter();

  @HostListener('click', ['$event']) onClicked(e) {
    this.opened.emit();
  }

  constructor(
    el: ElementRef,
  ) {
    el.nativeElement.id = 'duelit-paypal';
    this.use();
  }

  use() {
    // tslint:disable-next-line: no-string-literal
    if (window['paypal']) {
      try {
        // tslint:disable-next-line: no-string-literal
        window['paypal'].use(['login'], (login) => {
          login.render({
            appid: environment.paypal.apiId,
            authend: environment.paypal.environment,
            scopes: 'email',
            containerid: 'duelit-paypal',
            locale: 'es-es',
            theme: 'neutral',
            text: 'Send Cash Out Request',
            fullPage: 'true',
            returnurl: environment.paypal.returnUrl
          });
        });
      } catch (err) {
        console.error(err);
        // todo: toastr
      }
    } else {
      console.error('Paypal not found');
    }
  }

}
