import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { CASHOUT_REQUEST_AMOUNT } from '@core/constants';
import { Observable } from 'rxjs';
import { shareReplay, take, tap } from 'rxjs/operators';
import { CreditsModel } from 'src/app/core/models/credits.model';
import { CreditsService } from 'src/app/core/services/credits.service';

@Component({
  selector: 'duelit-redeem',
  templateUrl: './redeem.component.html',
  styleUrls: ['./redeem.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RedeemComponent implements OnInit {
  credits$: Observable<CreditsModel>;
  private credits: CreditsModel;
  cashOutForm: FormGroup;


  constructor(
    private creditsService: CreditsService,
    private fb: FormBuilder
  ) { }

  ngOnInit(): void {
    this.credits$ = this.creditsService.getCredits().pipe(tap(credits => {
      this.credits = credits;

      this.cashOutForm = this.fb.group({
        cashOutAmount: new FormControl(0, [
          Validators.max(this.maxReemableDollars(credits.virtualAmount, credits.coinsRateConversion)),
          Validators.pattern(/^[0-9]*$/)
        ])
      });

      this.cashOutForm.get('cashOutAmount').valueChanges.subscribe(value => localStorage.setItem(CASHOUT_REQUEST_AMOUNT, value));
    }), shareReplay(1), take(1));
  }

  maxReemableDollars(coins: number, coinsRateConversion: number) {
    return Math.floor(coins * coinsRateConversion);
  }

  transactionFee(): number {
    const cashout = this.cashOutForm.get('cashOutAmount').value;

    if (cashout < 35) {
      return 1;
    } else if (cashout >= 35) {
      return Math.round(2.9 * cashout) / 100;
    }

    return 0;
  }

  reedemableValue(): number {
    const cashout = this.cashOutForm.get('cashOutAmount').value;

    return cashout - this.transactionFee();
  }

  coinsToDeduct(): number {
    const cashout = this.cashOutForm.get('cashOutAmount').value;

    return Math.ceil(cashout / this.credits.coinsRateConversion);
  }
}
